@font-face {
  font-family: "Raleway";
  src: url(./fonts/Raleway-Regular.ttf);
}

@font-face {
  font-family: "Raleway-semibold";
  src: url(./fonts/Raleway-SemiBold.ttf);
}

body {
  font-family: "Raleway", sans-serif;
  overflow-x: hidden;
}

.flex {
  display: -ms-flexbox;
  display: flex;
}

.flex .flex-item {
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex-col {
  -ms-flex-direction: column;
      flex-direction: column;
}

.flex-center-90 {
  -ms-flex-align: center;
      align-items: center;
}

.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
}

.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}



